import React, { Component } from 'react';
import './Card.css';
import './WorkoutCard.css';
import '../styles/shadows.css';

class WorkoutCard extends Component {
  clicked = () => {
    this.props.clicked(this.props);  
  }

  render() {
    let cardColor = 'blue';
    let colors = {
      red: '#E1413C',
      blue: '#0598EC',
      yellow: '#F8B23D',
      green: '#009D60',
      purple: '#762182',
      gray: '#515969',
      tealBlue: '#005F78'
    }

    switch(this.props.category) {
      case('Warmup'):
        cardColor = colors.blue;
        break;
      case('Cardio'):
        cardColor = colors.red;
        break;
      case('Chest'):
        cardColor = colors.green;
        break;
      case('Back'):
        cardColor = colors.yellow;
        break;
      case('Legs'):
        cardColor = colors.purple;
        break;
      case('Core'):
        cardColor = colors.tealBlue;
        break;
      default:
        break;
    }

    let catCardStyle = {
      backgroundColor: cardColor,
    }

    let deleteCover = '';

    if (this.props.isDragging && this.props.draggingOver === null) {
      deleteCover = <div className="workout-card__delete-cover">Remove</div>;
    }

    return (
      <div className="workout-card shadow-2" >
        {deleteCover}
        <div className="workout-card__cat" style={catCardStyle}>
          {this.props.category}
        </div>
        <div className="workout-card__block">{this.props.name}</div>
        <div className="workout-card__block">
          <select defaultValue="Reps">
            <option value="Reps">Reps:</option>
            <option value="Seconds">Seconds:</option>
            <option value="Minutes">Minutes:</option>
          </select>
        {/*</div>
        <div className="workout-card__count">*/}
          <select defaultValue="10">
            <option value="1">1</option>
            <option value="2">2</option>
            <option value="3">3</option>
            <option value="4">4</option>
            <option value="5">5</option>
            <option value="6">6</option>
            <option value="7">7</option>
            <option value="8">8</option>
            <option value="9">9</option>
            <option value="10">10</option>
            <option value="12">12</option>
            <option value="15">15</option>
            <option value="20">20</option>
            <option value="25">25</option>
            <option value="30">30</option>
            <option value="40">40</option>
            <option value="50">50</option>
            <option value="60">60</option>
            <option value="70">70</option>
            <option value="80">80</option>
            <option value="90">90</option>
            <option value="100">100</option>
          </select>
        </div>
        {/*<div className="workout-card__block">Sets:&nbsp; 
          <select defaultValue="3">
            <option value="1">1</option>
            <option value="2">2</option>
            <option value="3">3</option>
            <option value="4">4</option>
            <option value="5">5</option>
            <option value="6">6</option>
            <option value="7">7</option>
            <option value="8">8</option>
            <option value="9">9</option>
            <option value="10">10</option>
          </select>
      </div>*/}
        {/*<div className="workout-card__block">Time: {this.props.time} min.</div>*/}
      </div>
    );
  }
}

export default WorkoutCard;
