import React, { Component } from 'react';
import './Card.css';
import '../styles/shadows.css';

class Card extends Component {
  clicked = () => {
    this.props.clicked(this.props);  
  }

  render() {
    let cardColor = 'blue';
    let colors = {
      red: '#E1413C',
      blue: '#0598EC',
      yellow: '#F8B23D',
      green: '#009D60',
      purple: '#762182',
      gray: '#515969',
      tealBlue: '#005F78'
    }

    switch(this.props.category) {
      case('Warmup'):
        cardColor = colors.blue;
        break;
      case('Cardio'):
        cardColor = colors.red;
        break;
      case('Chest'):
        cardColor = colors.green;
        break;
      case('Back'):
        cardColor = colors.yellow;
        break;
      case('Legs'):
        cardColor = colors.purple;
        break;
      case('Core'):
        cardColor = colors.tealBlue;
        break;
      default:
        break;
    }

    let catCardStyle = {
      backgroundColor: cardColor,
    }

    return (
      <div className="card shadow-2" onClick={this.clicked}>
        <span className="card__title">{this.props.name}</span>
        <br />
        <div className="card__bottom" style={catCardStyle}>
          {this.props.category}
        </div>
      </div>
    );
  }
}

export default Card;
